import React, { useEffect, useState, useMemo } from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Spinner from "react-bootstrap/Spinner";
import Modal from "react-bootstrap/Modal";
import { useAuth } from "../contexts/AuthContext";
import { toast } from "react-toastify";

const USER_FIELDS = [
  { key: "userId", label: "User ID" },
  { key: "userName", label: "User Name" },
  { key: "userDescription", label: "User Description" },
  { key: "name1", label: "Name 1" },
  { key: "name2", label: "Name 2" },
  { key: "active", label: "Active" },
];

function compare(a, b, key, direction) {
  if (a[key] == null && b[key] == null) return 0;
  if (a[key] == null) return 1;
  if (b[key] == null) return -1;
  if (typeof a[key] === "string") {
    return direction === "asc"
      ? a[key].localeCompare(b[key])
      : b[key].localeCompare(a[key]);
  }
  return direction === "asc" ? a[key] - b[key] : b[key] - a[key];
}

const UserCard = ({ user, onSave }) => {
  const [show, setShow] = useState(false);
  return (
    <>
      <Card
        className="mb-3 shadow-sm user-card-mini text-truncate"
        style={{ cursor: "pointer", minWidth: 0, maxWidth: 260, width: "100%" }}
        onClick={() => setShow(true)}
      >
        <Card.Body className="p-2 d-flex flex-column align-items-start justify-content-between">
          <div className="mb-1 text-truncate" style={{ width: "100%" }}><strong>ID:</strong> {user.userId}</div>
          <div className="mb-1 text-truncate" style={{ width: "100%" }}><strong>Name:</strong> {user.userName}</div>
          <div className="mb-2 text-truncate" style={{ width: "100%", fontSize: "0.85em", color: "#888" }}><strong>Active:</strong> {user.active ? "Yes" : "No"}</div>
          <Button variant="outline-primary" size="sm" className="align-self-end" onClick={e => { e.stopPropagation(); setShow(true); }}>Details</Button>
        </Card.Body>
      </Card>
      <UserDetailsModal show={show} onHide={() => setShow(false)} user={user} onSave={onSave} />
    </>
  );
};

function UserDetailsModal({ show, onHide, user, onSave, mode = "edit" }) {
  const [form, setForm] = useState(user || {});
  const [saving, setSaving] = useState(false);
  const { authenticatedFetch } = useAuth();

  useEffect(() => { setForm(user || {}); }, [user, show]);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setForm((f) => ({
      ...f,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const handleSave = async () => {
    setSaving(true);
    try {
      const baseUrl = import.meta.env.VITE_API_BASE_URL || "";
      let resp, updated;
      resp = await authenticatedFetch(`${baseUrl}/v2/api/users`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });
      if (!resp.ok) throw new Error("Failed to save user");
      updated = await resp.json();
      toast.success("User updated");
      onSave(updated);
      onHide();
    } catch (e) {
      toast.error(e.message || "Error saving user");
    } finally {
      setSaving(false);
    }
  };

  return (
    <Modal show={show} onHide={onHide} size="lg" centered contentClassName="user-modal-responsive">
      <Modal.Header closeButton>
        <Modal.Title>User Details</Modal.Title>
      </Modal.Header>
      <Modal.Body style={{ maxHeight: '60vh', overflowY: 'auto', minHeight: 200 }}>
        <Form>
          {USER_FIELDS.map(({ key, label }) => (
            <Form.Group key={key} className="mb-3" as={Row}>
              <Form.Label column sm={4} xs={5} style={{ fontWeight: 500 }}>{label}</Form.Label>
              <Col sm={8} xs={7}>
                {key === "active" ? (
                  <Form.Check
                    type="checkbox"
                    name={key}
                    checked={!!form[key]}
                    onChange={handleChange}
                    disabled={saving}
                  />
                ) : (
                  <Form.Control
                    name={key}
                    value={form[key] ?? ""}
                    onChange={handleChange}
                    disabled={saving || key === "userId"}
                    readOnly={key === "userId"}
                    type={typeof form[key] === "number" ? "number" : "text"}
                  />
                )}
              </Col>
            </Form.Group>
          ))}
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="primary" onClick={handleSave} disabled={saving}>
          {saving ? <Spinner size="sm" animation="border" /> : "Save"}
        </Button>
        <Button variant="secondary" onClick={onHide} disabled={saving}>Cancel</Button>
      </Modal.Footer>
    </Modal>
  );
}

const UsersScreen = () => {
  const { user, authenticatedFetch } = useAuth();
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [sort, setSort] = useState({ key: "userId", direction: "asc" });
  const [filter, setFilter] = useState({ userName: "" });

  useEffect(() => {
    setLoading(true);
    const baseUrl = import.meta.env.VITE_API_BASE_URL || "";
    authenticatedFetch(`${baseUrl}/v2/api/users`)
      .then((r) => {
        if (!r.ok) throw new Error("Failed to fetch users");
        return r.json();
      })
      .then((data) => setUsers(data))
      .catch((e) => toast.error(e.message || "Error loading users"))
      .finally(() => setLoading(false));
  }, [authenticatedFetch]);

  const filtered = useMemo(() => {
    return users.filter((u) => {
      const userNameMatch = !filter.userName || (u.userName || "").toLowerCase().includes(filter.userName.toLowerCase());
      return userNameMatch;
    });
  }, [users, filter]);

  const sorted = useMemo(() => {
    return [...filtered].sort((a, b) => compare(a, b, sort.key, sort.direction));
  }, [filtered, sort]);

  const handleSave = (updated) => {
    setUsers((us) => us.map((u) => (u.userId === updated.userId ? updated : u)));
  };

  if (!user || !(user.roles?.includes("admin"))) {
    return <div className="mt-4 text-danger">Access denied.</div>;
  }

  return (
    <div className="container mt-4">
      <h2 className="mb-3">Users</h2>
      <Form className="mb-3">
        <Row className="g-2">
          <Col xs={12} sm={6} md={4} lg={3} xl={2}>
            <Form.Control
              size="sm"
              placeholder="Filter by User Name"
              value={filter.userName}
              onChange={e => setFilter(f => ({ ...f, userName: e.target.value }))}
            />
          </Col>
        </Row>
      </Form>
      {loading ? (
        <div className="text-center my-5">
          <Spinner animation="border" />
        </div>
      ) : (
        <Row className="g-2">
          {sorted.map((user) => (
            <Col key={user.userId} xs={12} sm={6} md={4} lg={3} xl={2} xxl={2}>
              <UserCard user={user} onSave={handleSave} />
            </Col>
          ))}
        </Row>
      )}
      {sorted.length === 0 && !loading && <div>No users found.</div>}
    </div>
  );
};

export default UsersScreen;
